.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
nav{
  background-color:rgb(112, 234, 13);
  padding:16px 32px;
  height: 100px;
  text-align: center;

}
nav a{
  margin-right:200px;
letter-spacing: 2px;
text-transform: capitalize;
  text-decoration: none;
  font-family:Georgia, 'Times New Roman', Times, serif;
  font-weight: bold;
  margin-top: 100px;
  
}
nav a:active{
  color:yellow;
  
}
nav a:visited{
  color:red;
}
nav a:hover{
  color: rgb(73, 6, 167);
}
h1{
  background-color: aqua;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
p{
  color:rgb(239, 11, 11);
  background-color:lightgreen;
  letter-spacing: 3px;
  font-family: 'Courier New', Courier, monospace;
  font-style: italic;
  padding: 20px;
  border: 2px solid cyan;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
